.features {
  padding: 200px 120px 0;

  @include device(tablet) {
    padding: 200px 30px 0;
  }
  @include device(smartphone) {
    padding: 200px 0 0;
  }

  &__section-headline {
    font-weight: 700;
    font-size: 56px;
    margin-bottom: 30px;
    text-transform: uppercase;

    @include device(tablet) {
      font-size: 36px;
    }
    @include device(smartphone) {
      font-size: 32px;
      padding: 0 30px;
    }
  }

  &__container {
    background-color: $c-green3;
    background-image: url("../images/pictures/features.png");
    background-position: right bottom;
    background-repeat: no-repeat;
    padding-bottom: 60px;
    background-size: 40%;

    @include device(tablet) {
      display: block;
      background-size: 50%;
      padding: 0 30px 120px;
    }
    @include device(smartphone) {
      padding: 0 30px 120px;
      background-position: center bottom;
      background-size: 100%;
      padding-bottom: 60%;
    }
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    padding: 0 100px;

    @include device(smartphone) {
      padding: 0;
    }
  }

  &__types {
    width: 50%;
    padding-top: 60px;

    @include device(tablet) {
      &--1,
      &--2 {
        display: none;
      }
    }
    @include device(smartphone) {
      width: 100%;
    }
  }

  &__title {
    color: $c-green1;
    font-weight: 700;
    font-size: 34px;
    text-transform: uppercase;
    @include device(tablet) {
      font-size: 18px;
    }
  }

  &__list {
    margin-top: 30px;
    list-style: disc inside;
    line-height: 28px;
    @include device(tablet) {
      font-size: 14px;
    }
  }

  &__nav {
    display: none;

    @include device(tablet) {
      display: block;
      margin: 50px 0 0 100px;
    }
    @include device(smartphone) {
      margin-left: 30%;
    }
  }

  &__numbers {
    transform: translate(30px, 15px);
  }

  &__current {
    display: inline-block;
    transform: translateY(-5px);
  }

  &__total,
  &__slash {
    color: $c-green1;
    font-size: 12px;
  }
}
